.App {
  text-align: center;
  max-width: 2000px;
  margin: 0 auto;
}

ul{
  list-style: none;
  cursor: pointer;
}

h1 {
  text-align: center;
}

h2, h3, h4, h5, h6, p {
  margin: 0;
  line-height: 1.2;
  font-family: "Quicksand",sans-serif;
  font-weight: normal;
  color: white;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.logo {
  width: 100px;
  height: 100px;
}

